import React from 'react';
import Record from './Record';
const data = require('../sampleSources/datasample.json')


const Records = () => {

  const records = data; 

  return (
    <div>
      {records.map(record => (
        <Record procedureName='record.procedureName' procedureCost='record.procedureCost' hospital='record.hospital' />
      ))}
    </div>
  );
};

export default Records;
